import { Directive, Input } from '@angular/core';
import {
  ControlValueAccessor, Validator
} from '@angular/forms';
import { FormFieldBase } from './form-base';

// Define a type for the options for better type safety
export interface IselectOption {
  value;
  label: string;
  disabled?: boolean; // Optional: if you want to disable specific options
}

@Directive({})
export class ToggleFieldBase
  extends FormFieldBase
  implements ControlValueAccessor, Validator {
  @Input() showRequiredType = true;
  @Input() labelPosition: 'left' | 'right' = 'left';
  @Input() activeColor: string = 'var(--app-color-primary)';
  @Input() inactiveColor: string = '#ccc';
  @Input() thumbColor: string = 'white';
  @Input() focusColor: string = 'var(--app-color-primary)';
  @Input() borderColor: string = '#aaa';

  constructor() {
    super();
  }

  // Add this method
  onCheckboxChange(event: Event) {
    const target = event.target as HTMLInputElement;

    this.control.setValue(target.checked, { emitEvent: true });
  }

  // --- Template Interaction ---
  // Need methods to bind to input events in the template
  onInput(event: Event): void {
    const target = event.target as HTMLInputElement;

    this.updateValue(target.value); // Use the helper from base class
  }

  onBlur(): void {
    this.handleBlur(); // Use the helper from base class
  }

  dispVals(vals) {
    return JSON.stringify(vals);
  }
}
